import { h, Component } from 'preact';
import { Router } from 'preact-router';
import { Header, Footer } from './layout';
import {
	Home,
	About,
	Team,
	Culture,
	PressAwards,
	Programs,
	AdultWorkforce,
	HighSchool,
	Extracurricular,
	Contact,
	SignUp,
} from './pages';

export default class App extends Component {

	/** Gets fired when the route changes.
	 *	@param {Object} event		"change" event from [preact-router](http://git.io/preact-router)
	 *	@param {string} event.url	The newly routed URL
	 */
	handleRoute = e => {
		if (typeof window !== 'undefined' && !e.url.includes('/team?mate')) {
			window.scroll(0, 0);
		}
		this.currentUrl = e.url;
	};

	componentWillMount() {
		if (typeof document !== 'undefined') {
			document.title = 'Operation Spark';
		}
	}

	render() {
		return (
			<div id="app">
				<Header />
				<Router onChange={this.handleRoute}>
					<Home path="/" />

					{/* About */}
					<About path="/about" />
					<Team path="/team" />
					<Culture path="/culture" />
					<PressAwards path="/awards-and-press" />

					{/* Programs */}
					<Programs path="/programs" />
					<AdultWorkforce path="/workforce" />
					<HighSchool path="/high-school" />
					<Extracurricular path = "/extracurricular/:name" />

					{/* Contact */}
					<Contact path="/contact" />
					<SignUp path="/sign-up" />

				</Router>
				<Footer />
			</div>
		);
	}
}
