import { h } from 'preact';
import { Link } from 'preact-router/match';

const Notification = ({
  id = 'notification',
  text = 'Check out our Summer Program',
  path,
}) => (
  <div id={id} className="notification-container">
    <div className="notification">
      <Link class="notification-text" href={path}>{text}</Link>
      <button
        className="notification-button--dismiss"
        onClick={() => {
          document.getElementById(id).remove();
        }}
      >
        x
      </button>
    </div>
  </div>
);

export { Notification };
